@media only screen
  and (min-width: 1200px) {
  h1 {
    font-size: 6rem !important;
    line-height: 5rem !important;
  }
  h3 {
    font-size: 2rem !important;
    margin: 12px 0px;
  }
  input {
    font-size: 6rem !important;
  }
  #container {
    margin: 22px 70px;
  }
  #input-section .left {
    width: calc(100% - 340px);
  }
}
@media only screen
  and (min-width: 968px) 
  and (max-width: 1200px) {
  h1 {
    font-size: 4rem !important;
    line-height: 4rem !important;
  }
  h3 {
    font-size: 2rem !important;
    margin: 12px 0px;
  }
  input {
    font-size: 6rem !important;
  }
  #container {
    margin: 22px 40px;
  }
  #input-section .left {
    width: calc(100% - 400px);
  }
}
@media only screen
  and (max-width: 968px) {
  h1.MuiTypography-root {
    font-size: 2rem !important;
    line-height: 2.2rem !important;
  }
  h3 {
    margin: 12px 0px;
  }
  h3#solution-count {
    font-size: 2rem;
  }
  input {
    font-size: 3rem !important;
  }
  #container {
    margin: 22px;
  }
  #input-section, #answer-section {
    flex-direction: column;
  }
  #input-section {
    align-items: flex-start;
  }
  #answer-section .MuiStack-root {
    height: 90px;
  }
  button.MuiIconButton-root {
    font-size: 2.2rem;
  }
  h4.operator {
    font-size: 1.6rem;
  }
  #advance-ops {
    flex-wrap: wrap;
  }
  button#button-op {
    min-width: 81px;
  }
  #advance-ops button {
    margin: 6px;
  }
}

h4 {
  margin: 0px;
}